import axios from 'axios'

export const fetchCurrencies = async () => {
    return await axios.get('https://www.cbr-xml-daily.ru/daily_json.js')
}

export const getCurrenciesForMoneyJs = async () => {
    return await axios.get('https://www.cbr-xml-daily.ru/latest.js').then(response => response.data)
}